@import url(https://fonts.googleapis.com/css2?family=Inter:wght@100..900&display=swap);
:root {
  --highlight-color: #ccd6f6;
  --secondary-color: #8892b0;
  --border-color: #64ffda;
}
* {
  margin: 0;
  padding: 0;
}
html {
  height: auto;
  scroll-behavior: smooth;
}
h1 {
  color: #f0f0f0e6;
}
body {
  background-color: #0a192f;
  background-color: var(--background-color);
  font-family: Inter, sans-serif;
}
p {
  color: #64ffda;
  color: var(--text-color);
  margin: 10px;
}
::selection {
  background-color: #64ffda;
  background-color: var(--accent-color);
  color: #0a192f;
  color: var(--background-color);
}
.btn,
a {
  transition: all 0.3s ease;
}
#desktop-nav {
  background-color: #0a192f;
  background-color: var(--background-color);
  color: #64ffda;
  color: var(--text-color);
  left: 0;
  position: relative;
  top: 0;
  width: 100%;
  z-index: 1000;
}
#desktop-nav .logo {
  margin-left: 20px;
}
.nav-links,
nav {
  display: flex;
}
.nav-links,
nav {
  color: #64ffda;
  color: var(--text-color);
}
nav {
  align-items: center;
  height: 13vh;
  justify-content: space-around;
}
.nav-links {
  font-size: 1.5rem;
  gap: 2rem;
  list-style: none;
}
.nav-links,
a {
  color: #64ffda;
  color: var(--text-color);
}
a {
  text-decoration: none;
}
a:hover {
  color: #ccd6f6;
  color: var(--highlight-color);
}
a:focus {
  transform: scale(0.9);
}
.logo {
  font-size: 1.5rem;
  font-weight: 900;
}
.name {
  color: #64ffda;
  color: var(--text-color);
  font-size: 2rem;
  font-weight: 500;
}
.logo:hover {
  cursor: default;
}
#hamburger-nav {
  background-color: #0a192f;
  background-color: var(--background-color);
  color: #64ffda;
  color: var(--text-color);
  left: 0;
  position: absolute;
  top: 0;
  width: 100%;
  z-index: 500;
}
.hamburger-menu {
  display: inline-block;
  position: relative;
}
.hamburger-icon {
  cursor: pointer;
  display: flex;
  flex-direction: column;
  height: 24px;
  justify-content: space-between;
  width: 30px;
}
.hamburger-icon:active {
  transform: scale(0.9);
}
.hamburger-icon span {
  background-color: #64ffda;
  background-color: var(--accent-color);
  height: 2px;
  transition: all 0.3 ease-in-out;
  width: 100%;
}
.menu-links {
  max-height: 0;
  overflow: hidden;
  position: absolute;
  right: 0;
  top: 100%;
  transition: all 0.3s ease-in-out;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}
.menu-links a {
  color: #0a192f;
  color: var(--background-color);
  display: block;
  font-size: 1.5rem;
  padding: 10px;
  text-align: center;
  text-decoration: none;
  transition: all 0.3 ease-in-out;
}
.menu-links a:active {
  background-color: #64ffda;
}
.menu-links li {
  list-style: none;
}
.menu-links.open {
  max-height: 300px;
}
.hamburger-icon.open span:first-child {
  transform: rotate(45deg) translate(10px, 5px);
}
.hamburger-icon.open span:nth-child(2) {
  opacity: 0;
}
.hamburger-icon.open span:last-child {
  transform: rotate(-45deg) translate(10px, -5px);
}
.hamburger-icon span:first-child {
  opacity: 1;
  transform: none;
}
section {
  box-sizing: border-box;
  height: 96vh;
  margin: 0 10rem;
  min-height: -webkit-fit-content;
  min-height: -moz-fit-content;
  min-height: fit-content;
  padding-top: 4vh;
}
#profile,
.section-container {
  display: flex;
}
#profile {
  gap: 5rem;
  justify-content: center;
  margin-bottom: 20px;
  margin-top: 20px;
}
.section__text {
  align-self: center;
  color: #f0f0f0;
  text-align: center;
}
.section__text__p1 {
  color: #64ffda;
  color: var(--text-color);
  margin: 10px;
  text-align: center;
}
.section__text__p2 {
  color: #8892b0;
  color: var(--secondary-color);
  font-size: 1.8rem;
  font-weight: 900;
  margin: 10px;
}
.title {
  color: #ccd6f6;
  color: var(--highlight-color);
  font-size: 3.5rem;
  text-align: center;
}
#socials-container {
  display: flex;
  gap: 1rem;
  justify-content: center;
  margin-top: 1rem;
}
.icon {
  cursor: pointer;
}
.check,
.edu,
.exp,
.icon {
  background-color: #64ffda;
  background-color: var(--accent-color);
  border: 1.5px solid #64ffda;
  border: 1.5px solid var(--accent-color);
  border-radius: 52%;
  height: 2rem;
  color: var(--background-color);
}
.exp-icon {
  border: 1.5px solid var(--background-color);
  color: var(--accent-color);
  border-radius: 52%;
  height: 2rem;
}
.ejs {
  padding: 5px;
  font-size: 1.2rem;
  height: 2rem;
  width: 2rem;
}
.ejs,
.ex,
.pg,
.rst {
  align-items: center;
  background-color: #64ffda;
  background-color: var(--accent-color);
  border-radius: 50%;
  color: #0a192f !important;
  color: var(--background-color) !important;
  cursor: default;
  display: flex;
  font-weight: 900;
  justify-content: center;
}
.ex,
.pg,
.rst {
  font-size: 1.5rem;
  height: 2.5rem;
  width: 2.5rem;
}
img {
  object-fit: cover;
}
.exp-logo {
  height: 2rem;
}
.icon:active {
  transform: scale(0.9);
}
.btn-container {
  display: flex;
  gap: 1rem;
  justify-content: center;
}
.btn {
  border-radius: 0.5rem;
  font-weight: 600;
  padding: 1rem;
  transition: all 0.3s ease;
  width: 8rem;
}
.btn:active {
  box-shadow: 0 7px 1px 0 #ccd6f6;
  box-shadow: var(--highlight-color) 0 7px 1px 0;
  letter-spacing: 0.6px;
  transform: scale(0.9);
}
.btn-color-1 {
  background-color: #ccd6f6;
  background-color: var(--highlight-color);
  border: 0.05px solid #64ffda !important;
  border: 0.05px solid var(--accent-color) !important;
  color: #0a192f;
  color: var(--background-color);
}
.btn-color-1:hover {
  background: #0a192f;
  background: var(--background-color);
  letter-spacing: 0.6px;
}
.btn-color-1:hover,
.btn-color-2 {
  color: #64ffda;
  color: var(--accent-color);
}
.btn-color-2 {
  border: 0.05px solid #64ffda !important;
  border: 0.05px solid var(--accent-color) !important;
}
.btn-color-2:hover {
  background: #0a192f;
  background: var(--background-color);
  border-color: #ccd6f6;
  border-color: var(--highlight-color);
  color: #64ffda;
  color: var(--accent-color);
  letter-spacing: 0.6px;
}
.btn-color-1:hover,
.btn-color-2:hover {
  cursor: pointer;
}
#about {
  position: relative;
}
.abt-title {
  color: #ccd6f6;
  color: var(--highlight-color);
  font-size: 3rem;
  margin-bottom: 35px;
  text-align: center;
}
.about-containers {
  gap: 2rem;
  margin-bottom: 2rem;
}
.about-details-container {
  flex-direction: column;
  justify-content: center;
}
.about-containers,
.about-details-container {
  display: flex;
}
.exp-edu {
  margin: 1rem;
}
.edu,
.exp {
  cursor: default;
}
.strong {
  color: #ccd6f6;
  color: var(--highlight-color);
}
.arrow {
  bottom: 2.5rem;
  position: absolute;
  right: -5rem;
}
.details-container {
  border: 0.03rem solid #64ffda;
  border: 0.03rem solid var(--border-color);
  border-radius: 0.8rem;
  color: #ccd6f6;
  color: var(--highlight-color);
  flex: 1 1;
  padding: 1.5rem;
  text-align: center;
}
.details-container p {
  color: #8892b0;
  color: var(--secondary-color);
}
.section-container {
  gap: 4rem;
}
.section__pic-container {
  height: 400px;
  margin: auto 0;
  width: 400px;
}
.text-container p {
  color: #8892b0;
  color: var(--secondary-color);
  font-size: 1.2rem;
  line-height: 1.5;
  text-align: justify;
}
#experience {
  position: relative;
}
.exp-title {
  font-size: 3rem;
  margin-bottom: 35px;
  text-align: center;
}
.exp-title,
.experience-sub-title {
  color: #ccd6f6;
  color: var(--highlight-color);
}
.experience-sub-title {
  font-size: 1.75rem;
  font-weight: 600;
  margin-bottom: 30px;
}
.experience-details-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.article-container {
  flex-direction: row;
  flex-wrap: wrap;
  gap: 2.5rem;
  text-align: center;
}
.article-container,
article {
  display: flex;
  justify-content: space-around;
}
article {
  align-items: center;
  flex-direction: column;
  gap: 0.5rem;
  width: 10rem;
}
.check,
article .icon {
  cursor: default;
}
#projects {
  position: relative;
  top: 100px;
}
.prj-title {
  color: #ccd6f6;
  color: var(--highlight-color);
  font-size: 3rem;
  margin-bottom: 35px;
  text-align: center;
}
.color-container {
  background-color: initial;
  border: #0000;
  transition: background-color 0.3s ease;
}
.color-container:hover {
  background-color: #ffffff1a;
}
.project-img {
  border-radius: 0.5rem;
  height: 200px;
  width: auto;
}
.project-title {
  color: #ccd6f6;
  color: var(--highlight-color);
  margin: 1rem;
}
.project-btn {
  border: 0.05rem solid #64ffda;
  border: 0.05rem solid var(--accent-color);
  color: #64ffda;
  color: var(--accent-color);
  margin-top: 1rem;
}
.project-text-container {
  text-align: center;
}
.project-text-container ol,
.project-text-container ul {
  color: #8892b0;
  color: var(--secondary-color);
  list-style-position: inside;
  margin: 15px auto;
  text-align: center;
}
.project-text-container p {
  color: #8892b0;
  color: var(--secondary-color);
  font-size: 1rem;
}
.prj-p {
  font-size: 14px !important;
  text-align: justify;
}
.p-detail {
  font-size: 0.9em !important;
  text-align: justify;
}
#contact {
  display: flex;
  margin-top: 2rem;
  flex-direction: column;
  height: 70vh;
  justify-content: center;
  position: relative;
  top: 100px;
}
.cnt-title {
  color: #ccd6f6;
  color: var(--highlight-color);
  font-size: 3rem;
  margin-bottom: 35px;
  text-align: center;
}
.contact-info-upper-container {
  background: (230, 230, 230);
  border: 0.05px solid #64ffda !important;
  border: 0.05px solid var(--accent-color) !important;
  border-radius: 0.8rem;
  display: flex;
  justify-content: center;
  margin: 2rem auto;
  padding: 0.5rem;
}
.contact-info-container {
  align-items: center;
  display: flex;
  gap: 0.5rem;
  justify-content: center;
  margin: 1rem;
}
.contact-info-container p {
  font-size: larger;
}
.contact-icon {
  cursor: pointer;
}
.cnt-a {
  letter-spacing: 0.1px;
}
#footer {
  margin-top: 50px;
  height: auto;
  position: relative;
  top: auto;
}
#footer p {
  color: #8892b0;
  color: var(--secondary-color);
  font-size: 1rem !important;
  text-align: center;
}

.up-arrow {
  background-color: var(--accent-color);
  border: 1.5px solid var(--accent-color);
  border-radius: 52%;
  height: 2rem;
  width: 2rem;
  cursor: pointer;
  color: var(--background-color);
}

.up-arrow:active {
  letter-spacing: 0.6px;
  transform: scale(0.9);
}

:root {
  --accent-color: #64ffda;
  --background-color: #0a192f;
  --highlight-color: #e6f1ff;
  --text-color: #64ffda;
  --button-font-size: 2rem;
}
@media screen and (max-width: 1400px) {
  .abt-title,
  .cnt-title,
  .exp-title,
  .prj-title {
    font-size: 2rem;
    font-size: var(--button-font-size);
  }
  #profile {
    height: 83vh;
    margin-bottom: 0;
  }
  .about-containers {
    flex-wrap: wrap;
  }
  #contact,
  #projects {
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
  }
  .p-detail {
    text-align: center;
  }
  .btn-color-1,
  .btn-color-2 {
    border: 0.1rem solid #64ffda;
    border: 0.1rem solid var(--accent-color);
    color: #64ffda;
    color: var(--accent-color);
  }
  .btn-color-1:hover,
  .btn-color-2:hover {
    cursor: pointer;
  }
  .btn-color-2:hover {
    background: #0a192f;
    background: var(--background-color);
    color: #64ffda;
    color: var(--accent-color);
  }
  .btn-color-1 {
    background-color: #e6f1ff;
    background-color: var(--highlight-color);
    border: 1px solid #64ffda;
    border: 1px solid var(--accent-color);
    color: #0a192f;
    color: var(--background-color);
  }
  .btn-color-1:hover {
    background: #0a192f;
    background: var(--background-color);
    border: 2px solid #64ffda;
    border: 2px solid var(--accent-color);
    color: #64ffda;
    color: var(--accent-color);
  }
  .btn-color-2 {
    background: none;
  }
  .btn-color-2:hover {
    border: 0.1rem solid #e6f1ff;
    border: 0.1rem solid var(--highlight-color);
  }
  .btn:active {
    box-shadow: 0 7px 1px 0 #e6f1ff;
    box-shadow: var(--highlight-color) 0 7px 1px 0;
    letter-spacing: 0.6px;
    transform: scale(0.9);
  }
  footer {
    height: 15vh;
  }
  footer p {
    color: #8892b0;
    color: var(--secondary-color);
    font-size: 1rem !important;
    padding: 1rem;
    text-align: center;
  }
  hr {
    color: #64ffda !important;
  }
}

@media screen and (max-width: 1200px) {
  #desktop-nav {
    display: none;
  }
  #hamburger-nav {
    display: flex;
  }
  #experience,
  .experience-details-container {
    margin-top: 2rem;
  }
  .section-container {
    display: block;
  }
  .arrow {
    display: none;
  }

  .section-container,
  section {
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
  }
  .abt-title,
  .cnt-title,
  .exp-title,
  .prj-title {
    font-size: 2rem;
  }
  section {
    margin: 0 5%;
  }
  .section__pic-container {
    height: 275px;
    margin: 0 auto 2rem;
    width: 275px;
  }
  .about-containers {
    margin-top: 0;
  }
  #profile {
    margin-bottom: 0;
    margin-top: 20px;
  }
  .btn-color-1,
  .btn-color-2 {
    border: 0.1rem solid #64ffda;
    border: 0.1rem solid var(--accent-color);
    color: #64ffda;
    color: var(--accent-color);
  }
  .btn-color-1:hover,
  .btn-color-2:hover {
    cursor: pointer;
  }
  .btn-color-2:hover {
    background: #0a192f;
    background: var(--background-color);
    color: #64ffda;
    color: var(--accent-color);
  }
  .btn-color-1 {
    background-color: #e6f1ff;
    background-color: var(--highlight-color);
    border: 1px solid #64ffda;
    border: 1px solid var(--accent-color);
    color: #0a192f;
    color: var(--background-color);
  }
  .btn-color-1:hover {
    background: #0a192f;
    background: var(--background-color);
    border: 2px solid #64ffda;
    border: 2px solid var(--accent-color);
    color: #64ffda;
    color: var(--accent-color);
  }
  .btn-color-2 {
    background: none;
  }
  .btn-color-2:hover {
    border: 0.1rem solid #e6f1ff;
    border: 0.1rem solid var(--highlight-color);
  }
  .btn:active {
    box-shadow: 0 7px 1px 0 #e6f1ff;
    box-shadow: var(--highlight-color) 0 7px 1px 0;
    letter-spacing: 0.6px;
    transform: scale(0.9);
  }
  footer {
    height: 15vh;
    margin-top: 6rem;
  }
  footer nav {
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    margin-bottom: 2rem;
  }
}
@media screen and (max-width: 600px) {
  #contact {
    height: 40vh;
  }
  #profile {
    margin-bottom: 0;
    margin-top: 20px;
  }
  article {
